
html,
body {
    width: 100%;
    height: 100%;
}

body {
    margin-top: 1%;
    margin-bottom: 1%;
    background: linear-gradient(-45deg, #f5ccf6, #f1eef9, #a168fe, #d279ee, #deb0df, 
                                #c1e3ff, #abc7ff, #8debff, #6cacff, #59e7de, #6be395, 
                                #54e38e, #41c7af, #99e5a2, #dff48f, #f8c390, #BC3d2f,
                                #f54646, #f53e84 );
    background-size: 400% 400%;
    animation: gradient 15s ease infinite;
}

@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }50% {
        background-position: 100% 50%;
    }100% {
        background-position: 0% 50%;
    }
}

h1 {
    font-size: 50px;
    margin-bottom: 1%;
    font-family: 'Space Mono', monospace;
}

h2 {
    font-size: 25px;
    margin-bottom: 1%;
    font-family: 'Space Mono', monospace;
}

button {
    // background-color: rgb(149, 103, 172);
    opacity: .4;
    border: none;
    display: inline-block;
    padding: .3rem ;
    margin: .1rem;
    font-size: 1.3rem;
    width: 35px;
    height: 35px;
    border-radius: .5rem;
    font-family: Arial, Helvetica, sans-serif;
}

textarea {
    color: aliceblue;
    width: 100%;
    height: 100px;
    resize: none;
    padding: .5rem;
    outline: none;
    border: 2px solid peru;
    border-radius: .5rem;
    font-size: 18px;
    font-family: Arial, Helvetica, sans-serif !important;
    opacity: 0.7;
    background-color: rgb(19, 18, 18);
}

.backspace, .shift, .space {
    font-size: 1rem;
    text-transform: capitalize;
}

.backspace {
    width: 100px;
}

.shift {
    width: 70px;
}
.space {
    width: 350px;
}

.quote-display {
    margin-bottom: 1rem;
    text-align: center;
    width: 400px; 
    margin: auto; 
    text-align: center;
    padding: 20px;
    border-radius: 20px;
    font-size: 1.25rem;
    user-select: none;
    font-family: Arial, Helvetica, sans-serif;
}

.quote-input {
    width: 100%;
    margin-right: 3rem;
    display: flex;
    justify-content: center;
    margin-bottom: 2rem;
}


.quote-input:focus {
    border-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.3rem;
} 

.keyboard-display {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
}

.pressedKey {
    opacity: 1;
    color: rgb(38, 34, 38);
}


.correct {
    color: rgb(7, 243, 7);
    font-weight: 700;
}

.incorrect {
    color: rgb(201, 0, 0);
    font-size: x-large;
    font-weight: 900;
    text-decoration: underline;
}

.app {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.wpm {
    font-size: 20px;
    font-family: 'Space Mono', monospace;;
}

.a-tags {
    margin-top: 15px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

.canvas {
    margin-left: 20px;
}


#control-center {
    // background-color: rgb(132, 87, 102);
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    margin-right: 20px;
}

#display-screen {
    color: whitesmoke;
    display: flex;
    flex-direction: column;
    border-style: solid;
    border-color: #000603;
    border-radius: 20px;
    height: 20%;
    align-items: center;
    background-color: rgb(19, 18, 18);
    font-weight: 200;
    opacity: 0.7;
}

#timer {
    font-family: 'Orbitron', sans-serif;
    font-size: 30px;
    font-weight: bold;
    color: rgb(3, 153, 3);
    margin-bottom: 20px;
    border-style: solid;
    border-color: #515654;
    border-radius: 50px;
    // background-color: #2c2d2d;
    background-color: rgb(19, 18, 18);
    padding: 20px;
    opacity: 0.7;
}

#past-run-text {
    margin-top: 20px;
    margin-bottom: 10px;
    font-size: 25px;
}

#input-display {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
}

#quoteInput {
    display: flex;
    width: 400px;
}

#past-wpm {
    margin-top: 10px;
    margin-bottom: 20px;
}


#wpm-factors  {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-evenly;
}

#audio-btn, #git-icon, #linkedin-icon {
    width: 40px;
    height: 40px;
}




